import React, {useState} from 'react';

import logo from '../../logo_jecas.jpg';
import { NavLink } from "react-router-dom";
import { FaHome,FaHandsHelping,FaAddressCard} from 'react-icons/fa';
import {GoOrganization} from 'react-icons/go';
import './Navbar.css';


function Navbar(){
  const [open,setOpen] = useState(false);

  
  const submenu = () => {
    if(open){
      return(
              <ul>
                <li>
                  <NavLink activeClassName='is-active' to="/Habitaciones">Habitaciones</NavLink>
                </li>
                <li>
                  <NavLink activeClassName='is-active' to="/services">Libreria</NavLink>
                </li>
              </ul>

      )
    }else return(<div></div>)
  }

  return (
      <nav>
          <div>
            <img src = {logo}></img>
          </div>
          <ul>
            <li>
              <NavLink activeClassName='is-active' exact = {true}to="/"> <FaHome size = "2em"/><span>Inicio</span></NavLink>
            </li>
            <li>
              <NavLink activeClassName='is-active'to="/about"><GoOrganization size = "2em"/><span>Nosotros</span></NavLink>
            </li>
            <li>
              <a>
                <FaHandsHelping size = "2em"/>
              <span onClick ={() => setOpen(!open)}>Servicios</span> 
              </a>
            </li>
                {submenu()}
            <li>
              <NavLink activeClassName='is-active' to="/contactus"><FaAddressCard size = "2em"/><span>Contactanos</span></NavLink>
            </li>
          </ul>
        </nav>
  );
}

export default Navbar;
