import React, {useState} from 'react';

import { useSpring, a } from'react-spring';
import './mainpage.css';
import Navbar from './NavbarComponent/Navbar'
import Habitacion from './HabitacionComponent/Habitacion'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft } from "@fortawesome/free-solid-svg-icons";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import { MemoryRouter } from 'react-router'
import About from './About';
import Contactus from './Contactus';
import Services from './Services';
import Home from './Home';

//import ReactCSSTransitionGroup from 'react-transition-group';
//var ReactCSSTransitionGroup = require('react-transition-group'); 

//var CSSTransitionGroup = require('react-transition-group/CSSTransitionGroup')
//

function MainPage() {

  const [visible,setVisible] = useState(window.innerWidth <= 760);
  const des = useSpring({
    to: {  x:visible?-265:0 },
    from: {x :visible? 0:-265},
  });


  return (
    <div className = "contenedor">
 <MemoryRouter>
    <a.div className = "animated-cont" style = {des}>
      <div className = "sub-con-animated">
        <Navbar/>
      </div>
    </a.div>
   <div className= "container">
        <button onClick = {() => setVisible(!visible)}><FontAwesomeIcon icon = {faBars} size = "3x"/></button>
        <Switch>
          <Route path="/habitaciones">
            <Habitacion />
          </Route>
 
          <Route path="/about">
            <About />
          </Route>
          <Route path="/contactus">
            <Contactus />
          </Route>
          <Route path="/services">
            <Services />
          </Route>

          <Route path="/">
            <Home />
          </Route>
        </Switch>
    </div>
    </MemoryRouter>
   </div>
  );
}

export default MainPage;
